@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section.DocTable{padding:20px;overflow-y:scroll}section.DocTable table{font-size:14px;margin:30px 0px}section.DocTable table thead.header{border:none;background-color:#444}section.DocTable table .field-name{font-family:'Roboto Mono', monospace;font-weight:700}section.DocTable h1{font-family:'Raleway', sans-serif;margin:40px 0px}section.DocTable h5{line-height:1.5em;font-weight:300}section.DocTable .msg-name-box{margin-top:40px;margin-bottom:5px;font-size:2em}section.DocTable .msg-name-box .msg-name-tag{background-color:#222;padding:4px;border-top-left-radius:2px;border-bottom-left-radius:2px;border:1px solid #222;color:#fff}section.DocTable .msg-description{font-weight:300}section.DocTable .msg-table{border-bottom:1px dotted #ddd}

.GraphView{width:100%;height:100%}.GraphView #tree-container{width:100%;height:100%}.GraphView #tree-container #tree-container-svg{width:100%;height:600px}

section.DocPage{width:100%;background-color:#303136;color:#fff}section.DocPage h1{font-family:'Raleway', sans-serif;margin:40px 0px}section.DocPage h5{line-height:1.5em;font-weight:300}section.DocPage .full-width-height{width:100%}section.DocPage .nav-path{padding-bottom:20px;padding-left:5px;margin-top:20px;font-size:.8em;font-family:'Roboto Mono', monospace;border-bottom:3px solid #FCBF49}section.DocPage .standard-description{margin-bottom:60px}section.DocPage .nav-item a{color:#888}section.DocPage .nav-item a:hover{color:#666}section.DocPage .hljs{background:none}

section.DocSearch{position:fixed;height:100%;width:16%;background-color:#202124;padding:20px}section.DocSearch .doc-search{margin-top:10px;width:100%}section.DocSearch .logo-div{padding-bottom:15px;border-bottom:1px solid #333}

.DocContentList{position:fixed;margin-top:150px;margin-left:10px;padding:10px;font-size:0.6em;border-left:2px solid #ddd;font-family:'Roboto Mono', monospace}.DocContentList ul{margin:0;padding:0;list-style:none}.DocContentList ul a{text-decoration:none;color:#ddd}.DocContentList ul li{margin:6px 0px}

html,body{height:100%;width:100%;font-family:'Roboto', sans-serif;overflow:visible}#root,.App{height:100%;width:100%}.nopadding{padding:0 !important;margin:0 !important}

